.bio-links {
  position: relative;
  display: inline-block;
  -webkit-transition: color 0.2s ease;
  transition: color 0.2s ease;
  text-decoration: none;  
}

.bio-links:hover{
  text-decoration: none;  
}

.bio-links::after, .bio-links::before {
  content: "";
  position: absolute;
  width: 0;
  right: 0;
  height: 8px;
  bottom: 8px;
  z-index: -1;
}

.bio-links:hover::after, .bio-links:hover::before {
  display: block;
  width: 100%;
  left: 0;
  bottom: 8px;
}

